@use '../util' as *;
@use'../globals/colors.scss' ;

.header{
    margin-top: rem(36);
    @include breakpoint(medium){
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
     &__title{
         
        @include breakpoint-down(small){
            padding-bottom: 1.2rem;
            border-bottom: 1px solid hsl(163, 72%, 41%);
        }
     }

     &__subtitle{
        font-size: rem(14);
        font-weight: 700;
        color: var(--dark-text1);
     }

     &__toggle{

     }

    
}

